	module.exports = {"data":{"markdownRemark":{"html":"<p>The user inputs several shopping items and quantities. The program returns a total cost.</p>\n<ul>\n<li>A shop contains at least 10 types of items.\nThe user can enter several shopping items, and the program returns a total cost. </li>\n<li>Items can \"On Special\" where they are X% off the normal price.\nAdd a discount to at least one of the items. </li>\n<li>Items can also be \"On Special\" where they are $X  off the normal price.\nAdd a discount to at least one of the items. </li>\n<li>Items can have both a percentage and a dollar discount, however only apply the highest discount to the item cost.\nApply a dollar and percentage discount to at least one item\nEg. a 5 dollar item can have a 10% off and a $2 sale, the item therefore is $3. </li>\n<li>The user can use a coupon code to discount all items by a percentage.\nEg. 10% off store wide.\nApply only the highest single discount to an item, either the coupon discount, the item dollar discount or the item percentage discount. </li>\n<li>The user can use a coupon code to discount a type of item by a percentage.\nEg. all vegetables by 3%\nSame rules as above apply.</li>\n<li>The user can only use one coupon. </li>\n</ul>\n<p><a href=\"../developers\">&#x3C; Prev</a> | <a href=\"../shopping2\">Next ></a></p>","frontmatter":{"path":"/shopping","title":"Shopping Cart: Console Application"}}},"pathContext":{}}

